.formContainer {
    margin: 0 auto;
    max-width: 600px;
}

.guardText {
    padding-top: 8px !important;
}

.cardPreview {
    border-radius: 6px;
    background-color: #f5f7f9;
    padding: 21px 32px 40px 16px;
}

.cardImage {
    border-radius: 4%/6%;
    max-width: 100%;
}

.hr {
    height: 0;
    border: 1px dotted rgba(11, 31, 53, .3);
    margin: 32px 0;
}
